<template>
  <div class="main_navs">
    <Header></Header>
    <div class="container mb-4" v-if="is_response">
      <!--      <div class="title_ans">-->
      <!--        Тест на темперамент-->
      <!--      </div>-->
      <div class="ans_card">
        <div class="row">
          <div class="col-xl-8">

            <div class="ans_title">{{an_res.title}}</div>
            <div class="ans_desc">
              {{an_res.description}}
            </div>

            <div class="link_group">
              <div>
                <a @click="getOnetest()" class="link_main decoration" href="#">
                  <div>
                    Testga qaytish
                  </div>
                </a>
              </div>
              <div>
                <router-link class="link_main_calk decoration" to="/start-test">
                  <div>
                    Boshqa testga kirish
                  </div>
                </router-link>
              </div>
            </div>
          </div>
          <div class="col-xl-4">
            <img :src="an_res.image_url" width="100%" class="ans_img" alt="">
          </div>
        </div>
      </div>
    </div>
    <div class="container" v-if="!is_response">
      <div class="on_test_title">{{$store.state.testsId.title}}</div>
      <div class="on-test">
        <div class="one_test_progress">
          <div class="on-test-range">
            <range-slider
                class="slider"
                min="1"
                :max="$store.state.test_one.length"
                step="1"
                v-model="activeIndex"
            >
            </range-slider>
          </div>
          <div >

            <div class="col-xl-12 on-test-card" v-for="(test, i) in  $store.state.test_one" :key="test.id">
              <div v-show="isShows==true||activeIndex == i" :class="{'activeShows':isShows}">
                <div>
                  <div class="on-test-quiz">
                    {{ test.title }}
                  </div>
                  <div class="answers row" v-show="test.is_many_variants">
                    <label   v-for="option in test.choices"
                             :key="option.id"
                             class="col-xl-4 ans_id "
                             :class="{'myCheck isActive':itemCheck.length==2}"
                    >
                      <input   v-model="option.active"
                               :value="option"
                               class=""
                               type="checkbox"
                               @input="isCheck(option)"
                      >
                      <span class="checkmark"></span>
                      <span class="ans_text">{{ option.text }}</span>
                    </label>
                  </div>
                  <div class="answers row" v-show="!test.is_many_variants"  >
                    <input type="text" v-show="test.choices.length==0" @input="textCreate()" v-model="help_text" class="form-control" placeholder="tanlangan yo‘nalish aniq yoziladi">

                    <label   v-for="option in test.choices"
                             :key="option.id"
                             class=" ans_id container_input"
                             :class="{'col-xl-4':test.choices.length>=7}"
                             :for="`option${option.id}${test.id}`"
                    >
                      <input   v-model="answers[i]"
                               :value="option"
                               class=""
                               type="radio"
                               :id="`option${option.id}${test.id}`"
                      >
                      <span class="checkmark"></span>
                      <!--                    <div class="test_circle">-->
                      <!--                      <div class="test_circle_child"></div>-->
                      <!--                    </div>-->
                      <span class="ans_text">{{ option.text }}</span>
                    </label>
                  </div>
                </div>
                <div class="on-test-button">
                  <button class="prev" @click="prevStart()">

                    <span>{{$t('prev')}}</span>

                  </button>
                  <button v-if="activeIndex==($store.state.test_one.length-1)" class="next actives" :class="{'actives':answers.length>activeIndex||help_text}" @click="endTest()">
                    <span>{{$t('end')}}</span>
                  </button>
                  <button v-else class="next actives" :class="{'actives':answers.length>activeIndex||help_text}" @click="nextStart(test,i)">
                    <span>{{$t('next')}}</span>
                  </button>
                </div>
              </div>
            </div>
          </div>

        </div>
      </div>
    </div>
    <Footer></Footer>
  </div>
</template>
<script>
import Header from "../../components/Header";
import Footer from "../../components/Footer";
import RangeSlider from 'vue-range-slider'
// you probably need to import built-in style
import 'vue-range-slider/dist/vue-range-slider.css'

export default {
  components: {Footer, Header, RangeSlider},
  data() {
    return {
      sliderValue: 1,
      value: 0,
      activeIndex: 0,
      activeIndex1: 0,
      max: 12,
      isActive: false,
      userResponses: [],
      answer: [],
      activeAn:[],
      an_res:{},
      is_response:false,
      option:'',
      answers:[],
      help_text:'',
      itemCheck:[],
      isShows:false
    }
  },
  mounted() {
    this.$store.dispatch('getQuizOne', this.$route.params.id)
    this.max = this.$store.state.test_one.length
    this.$store.dispatch('getTestsId',this.$route.params.id)

  },
  methods: {
    textCreate(){
      var todayDate = new Date().toISOString().slice(0, 10);

      this.answer[this.activeIndex1] = {
        "question": this.$store.state.test_one[this.sliderValue - 1].id,
        "choices":this.itemCheck,
        "text":this.help_text,
        "date_taken":todayDate
      }
    },
    isCheck(index){
      console.log(index)
      var todayDate = new Date().toISOString().slice(0, 10);
      var indexx = this.itemCheck.indexOf(index.id);

      if (indexx === -1) {
        // If the item is not found, push it into the array
        this.itemCheck.push(index.id);
      } else {
        // If the item is found, remove it from the array
        this.itemCheck.splice(indexx, 1);
      }
      this.answer[this.activeIndex1] = {
        "question": this.$store.state.test_one[this.sliderValue - 1].id,
        "choices":this.itemCheck,
        "text":this.help_text,
        "date_taken":todayDate
      }
      console.log(this.itemCheck)
    },
    getAns(item) {

      var todayDate = new Date().toISOString().slice(0, 10);
      this.answer[this.sliderValue - 1] = {
        "question": this.$store.state.test_one[this.sliderValue - 1].id,
        "choices": [item.id],
        "text":this.help_text,
        "date_taken":todayDate,

      }

    },
    nextStart(test,i) {
      var todayDate = new Date().toISOString().slice(0, 10);
      if (this.answers[i]===undefined){
        this.activeIndex1--
      }
      if(this.answers[i]!=undefined){
        this.answer[this.activeIndex1] = {
          "question":test.id,
          "choices": [this.answers[i].id],
          "text":this.help_text,
          "date_taken":todayDate
        }
      }
      console.log(this.answers[i].ans_type, this.$store.state.test_one)
      if(this.answers[i].ans_type==3){
        this.$store.state.test_one.splice(2, 2);
      }
      if(this.answers[i].ans_type==4){
        this.$store.state.test_one.splice(1, 1);
        this.$store.state.test_one.splice(2, 1);
        this.$store.state.test_one.splice(2, 5);
      }
      if(this.answers[i].ans_type==5){
        this.$store.state.test_one.splice(1, 2);
        this.$store.state.test_one.splice(2, 5);
      }
      if(this.answers[i].ans_type==10){
        this.activeIndex=6
      }
      if(this.answers[i].ans_type==11){
        this.activeIndex=6
      }
      if(this.answers[i].ans_type==7){
        this.$store.state.test_one.splice(5, 2);
      }
      if(this.answers[i].ans_type==8){
        this.$store.state.test_one.splice(3, 1);
        this.$store.state.test_one.splice(5, 1);
      }
      if(this.answers[i].ans_type==9){
        this.$store.state.test_one.splice(4, 2);
      }

      if (this.activeIndex < this.$store.state.test_one.length) {
        this.activeIndex++
        this.activeIndex1++
      }


    },
    endTest() {
      this.activeIndex++
      this.isShows=true
      window.scrollTo({ top: 0, behavior: 'smooth' });
      this.$axiosDefault.post(`test-result/`,
          {
            "answers": this.answer,
            "test_id": this.$route.params.id,
            "user_id":this.$store.state.me.id
          }
          ,
          {
            headers: {
              'Authorization': `Bearer ${localStorage.getItem('reg_token')}`,
            }
          })
          .then(res=>{
            this.an_res=res.data.result
            this.is_response=true
            this.$toast("Javoblar qabul qilindi", {
              timeout: 2000,
              type:"success"
            });
          })
          .catch(()=>{
            this.$toast("Javoblar to'liq belgilanmagan", {
              timeout: 2000,
              type:"error"
            });
          })
    },
    getOnetest(){
      this.is_response=false
      this.activeIndex=0
      this.answer=[]
      this.answers=[]

    },
    prevStart() {

      if(this.activeIndex==7){

        this.activeIndex=2
      }
      if(this.activeIndex==1){
        this.$store.dispatch('getQuizOne', this.$route.params.id)
        this.max = this.$store.state.test_one.length
        this.$store.dispatch('getTestsId',this.$route.params.id)
        this.activeIndex=0
      }
      if (this.activeIndex >= 1) {
        this.activeIndex--
      }

    }
  }
}
</script>
